<template>
    <!--begin::Modal-->
    <div class="modal fade" id="formModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel">{{form.id ?'تعديل':'إضافة'}}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                        </button>
                </div>
                <div class="modal-body row">
                    <div class="col-6 mt-3" v-if="types&&types.length">
                        <label>نوع الإيراد</label>
                        <v-select dir="rtl" label="name" class="vselect" style="width: 100%;" :options="types" :reduce="val => val.id" v-model="form.type_id"></v-select>
                    </div>
                    <div class="col-md-6 mt-3">
                        <label>المبلغ</label>
                        <input v-model="form.money" type="number" autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('money') }">
                        <has-error :form="form" field="money"></has-error>
                    </div>
                    <div class="col-md-6 mt-3">
                        <label >التاريخ</label>
                        <Datepicker input-class="form-control" v-model="form.created_at" format="dd-MM-yyyy"></Datepicker>
                        <has-error :form="form" field="money"></has-error>
                    </div> 
                    <div class="col-6 mt-3">
                        <label>البيان</label>
                        <input dir="rtl" v-model="form.input" type="text" autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('input') }">
                        <has-error :form="form" field="input"></has-error>
                    </div>
                    <div class="col-12 mt-3">
                        <label>ملاحظات</label>
                        <textarea dir="rtl" v-model="form.note" rows="2" cols="50" type="text" :class="{ 'is-invalid': form.errors.has('note') }"
                            autocomplete="off" class="form-control"></textarea>
                        <has-error :form="form" field="note"></has-error>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal">إغلاق</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary">تعديل</button>
                    <button v-else type="submit" class="btn btn-primary">حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>
<script>
import { Form, HasError } from 'vform'
import axios from 'axios';
import Datepicker from 'vuejs-datepicker';
import "vue-select/dist/vue-select.css";
export default {
    components: { HasError,Datepicker },
    props: ['fetchData'],
    data() {
        return {
            form: new Form({
                id: null,
                type_id: null,
                money: null,
                input: null,
                note: null,
                created_at: new Date().toLocaleString(),
            }),
            types: null,
        };
    },
    created() {
        this.getInfo();
    },
    methods: {
        create() {
            this.form.reset();
            $('#formModal').modal('show');
        },
        getInfo() {
            axios.get("/api/input_types", { params: { paginate: 0 } }).then(({ data }) => {
                this.types = data.success.input_types;
            }).catch((error) => { console.log("Error......") })
        },
        store() {
            this.form.post("/api/inputs").then(({ data }) => {
                if (data.message) {
                    this.$swal.fire({ icon: 'error', title: "Error Message", text: data.message + '!' });
                } else {
                    this.fetchData();
                    $('#formModal').modal('hide');
                }
            }).catch((error) => { console.log("Error......") })
        },
        edit(form) {
            this.form.fill(form);
            $('#formModal').modal('show');
        },
        update() {
            this.form.put("/api/inputs/" + this.form.id).then((res) => {
                this.fetchData();
                $('#formModal').modal('hide');
            }).catch((error) => { console.log("Error......") })
        },
        destroy(id) {
            this.$swal.fire({
                title:'تحذير',text:'هل أنت متأكد من الحذف',
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: 'إلغاء',
                confirmButtonText: 'نعم , احذف',
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/inputs/' + id).then(response => {
                        if(response.data.message){
                            this.$swal.fire({icon: 'error',title: 'Oops...',text: response.data.message+'!',
                                footer: '<a href>Why do I have this issue?</a>'
                            });
                        }else{
                            this.$swal.fire('تم','تم الحذف بنجاح','success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({ icon: 'error', title: 'Oops...', text: 'Something went wrong!' });
                    });
                }
            })
        },
    },
}
</script>
