<template>
    <layout-default>
        <div class="page-content">
            <div class="container-fluid">
                <!-- start page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="page-title-box d-flex align-items-center justify-content-between">
                            <h4 class="mb-0">جدول الإيرادات</h4>
                            <div class="page-title-right">
                                <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                                        + إضافة إيراد جديد
                                    </button>
                                <form-modal ref="FormModal" :fetchData="fetchData" />
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="card">
                            <div v-if="inputs&&inputs.data.length" class="card-body">
                                <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                    <thead>
                                        <tr>
                                            <th>نوع الإيراد</th>
                                            <th>المبلغ</th>
                                            <th>بواسطة</th>
                                            <th>التاريخ</th>
                                            <th>البيان</th>
                                            <th>ملاحظات</th>
                                            <th>خيارات</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(input,index) in inputs.data">
                                            <td>{{input.type.name}}</td>
                                            <td>{{moneyFormat(input.money,0)}}</td>
                                            <td>{{input.user.name}}</td>
                                            <td>{{input.created_at| moment("DD/MM/YYYY")}}</td>
                                            <td>{{input.input}}</td>
                                            <td>{{input.note}}</td>
                                            <td>
                                                <div class="btn-group">
                                                    <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                            المزيد <i class="mdi mdi-chevron-down"></i>
                                                        </button>
                                                    <div class="dropdown-menu" style="min-width: 120px;">
                                                        <a @click="$refs.FormModal.edit(input)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                <i class="far fa-edit"></i> تعديل
                                                            </a>
                                                        <a @click="$refs.FormModal.destroy(input.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                <i class="far fa-trash-alt"></i> حذف
                                                            </a>
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <pagination-nav class="mt-3" v-if="inputs.last_page>1" :pageSize="inputs.last_page" :currentPage="inputs.current_page" :GoToPage="fetchData" />
                            </div>
                            <div v-else class="card-body">
                                <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                                <p v-else class="card-title-desc" style="text-align: center;">
                                    لا يوجد إيرادات حتى الآن
                                </p>
                            </div>
                        </div>
                    </div>
                    <!-- end col -->
                </div>
                <!-- end row -->
            </div>
            <!-- container-fluid -->
        </div>
    </layout-default>
</template>
<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/input/add';
import axios from 'axios';
export default {
    components: { LayoutDefault, PaginationNav, FormModal },
    data() {
        return { inputs: null, loader: false }
    },
    created() {
        this.fetchData();
    },
    methods: {
        fetchData(num = 1) {
            this.inputs = null;
            this.loader = true;
            let data = { params: { page: num } };
            axios.get('/api/inputs', data).then(({ data }) => {
                this.inputs = data.success.inputs;
                if (!data.success.inputs.data.length && num != 1) {
                    this.fetchData(1)
                }
                this.loader = false;
            });
        },
    },
}
</script>